
import {defineComponent} from "vue";

export default defineComponent({
  name: "SetTime",
  data() {
    return {
      timeValues: [15, 30, 60, 120],
    }
  },
  computed: {
    currentValue() {
      return this.$store.state.timer;
    }
  },
  methods: {
    setValue(number: number) {
      this.$store.commit('setTimer', number);
      this.$store.commit('toggleReload');
    }
  }
})
